import constants from './constants.js';
import Worker from 'worker-loader!./worker.js';
import { Chart } from 'chart.js';
import ChartHist from './chart-histogram.js';
import { annotation } from 'chartjs-plugin-annotation';
import { Decimal } from 'decimal.js';
import feather from 'feather-icons'

//// Constants

const POOL_SIZE = 10;

//// Init

Decimal.set({ precision: 13, rounding: 6 })
window.Decimal = Decimal

Chart.plugins.register({
  annotation: annotation
});

// array to indicate workers in pool are ready
var workerPoolReadyFlag = new Array(POOL_SIZE).fill(false);

// start workers
var workerPool = new Array(POOL_SIZE).fill(undefined).map((_, i) => { // eslint-disable-line no-unused-vars
  var worker = new Worker;
  
  worker.onmessage = function(e) {
    workerPoolReadyFlag[e.data] = true
  }
  
  worker.postMessage({ init: true, i: i });
  return worker
})

// check if all workers are aready to use
const checkPoolStatus = () => {
  if (workerPoolReadyFlag.reduce((a, c) => a && c, true)) {
    console.log("pool ready!")
  } else {
    window.setTimeout(() => {
      checkPoolStatus()
    }, 100)
  }
}
checkPoolStatus()


//// Utils

// uniq id generator
// https://stackoverflow.com/questions/105034/how-to-create-a-guid-uuid
function broofa() {
  let template = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx';
  //let template = 'xxxxxx'
  return template.replace(/[xy]/g, function(c) {
    var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
    return v.toString(16);
  });
}


export default {
  name: 'App',
  components: {
    //HelloWorld
  }, 
  watch: {
    // notebook: {
    //   handler (val) { // eslint-disable-line no-unused-vars
    //     this.preFill()
    //   },
    //   deep: false
    // },
  },
  data () {
    return {
      notebook: {
        cells: [
          {
            expr: 'X1',
            res: {}
          },
          {
            expr: 'X1',
            res: {}
          }
        ]
      },
      histCharts: [],
      stateControl: {
        processing: false
      },
      permalink: ''
    }
  },
  methods: {
    genPermalink () {
      this.permalink = window.location.origin + window.location.pathname + '#/' + JSON.stringify(this.notebook)
    },
    preFill () {
      this.$nextTick(() => {
        this.expressionsVars.forEach((rowVars, i) => {
          let cell = this.notebook.cells[i]
          //console.log(rowVars, i, cell)
          cell.data = cell.data || []

          rowVars.forEach(rowVar => {
            let quantity = cell.data.filter(item => item.var === rowVar)[0] || cell.data[cell.data.push({}) - 1]
            quantity.var = quantity.var || rowVar
            quantity.dist = quantity.dist || "Uniform"
            quantity.args = quantity.args || new Array(3).fill(0)            
          })

        })
      })
    },
    calcCell (i) { // eslint-disable-line no-unused-vars
      if (this.stateControl.processing) return
      this.stateControl.processing = true
      let cell = this.notebook.cells[i]

      const HIST_SIZE = constants.HIST_SIZE
      const N_BURSTS = constants.N_BURSTS
      const BURST_SIZE = constants.BURST_SIZE

      // init monitoring params
      var M = 0
      
      // pointer to charts array
      var histCharts = this.histCharts

      // this variable will be averaged in-place
      // first is undefined, than will be the array
      let resAverage = undefined

      const Mend = BURST_SIZE * N_BURSTS * POOL_SIZE

      // compute realtime parameters average
      workerPool.forEach((worker) => {

        if (!this.stateControl.processing) return

        worker.onerror = () => {

          // TODO: recovery worker pool
          if (!this.stateControl.processing) return
          this.stateControl.processing = false
          alert('Error. Check provided expression and parameters.')
        }

        worker.onmessage = (e) => {
          // wasm function return only one array with values:
          //
          // [0]  : lower probabilistic coverage interval 
          // [1]  : higher probabilistic coverage interval 
          // [2]  : the average value
          // [3]  : min value
          // [4]  : max value
          // [5..]: histogram
          let res = [].slice.call(e.data)

          resAverage = resAverage || new Array(res.length).fill(0)

          // weigth average in-place the array 
          resAverage = resAverage.map((item, i) => (item * M + res[i] * BURST_SIZE) / (M + BURST_SIZE))
          
          M += BURST_SIZE

          let data = resAverage.slice(6)

          histCharts[i] = histCharts[i] || new Chart(this.$refs['hist_'+i].getContext('2d'), ChartHist(data)); // chart
          
          let histChart = histCharts[i]
          
          let piecewiseFreq = []
          let piecewiseX = []

          // fill piecewise distributions
          data.forEach((freq, i) => {
            let labelVal = resAverage[3] + i * (resAverage[4]-resAverage[3])/HIST_SIZE

            // if flagged as export, gerenate arrays 
            // to build piecewise distribution
            if (M === Mend) {
              piecewiseFreq.push(i === 0 ? (freq/Mend*100) : (freq/Mend*100 + piecewiseFreq[i-1]))
              piecewiseX.push(labelVal)
            }
            
            // update chart labels and data
            histChart.data.labels[i] = (Math.abs(labelVal) < 0.1 ? labelVal.toExponential(3) : labelVal.toFixed(3)) + (' ' + cell.unit || '')
            histChart.data.datasets[0].data[i] = Math.round(freq)
          })

          // build the piecewise distribution string representation
          if (M === Mend) {

            // fix possible float point error
            piecewiseFreq[piecewiseFreq.length-1] = 1

            cell.piecewise = 
              piecewiseFreq.reduce((a, c) => a + '|' + c.toExponential(3), '').slice(1) +
              ';' + 
              piecewiseX[0].toExponential(3)+ '|' + piecewiseX[piecewiseX.length-1].toExponential(3)
            
            console.log(cell.piecewise)
          }

          // // clean any piecewise data is flag to not export
          // if (!M === Mend) {
          //   delete cell.piecewise
          //   delete cell.export
          // }

          // update chart with final results
          if (M === Mend) {
            //this.$nextTick(() => {
              const sdDec = new Decimal(resAverage[5])
              cell.sd = sdDec.toSD(2).toString()

              const pcilDec = new Decimal(resAverage[0])
              const pcihDec = new Decimal(resAverage[1])
              const yDec = new Decimal(resAverage[2])


              cell.pcil = pcilDec.toPrecision(4).toString()
              cell.pcih = pcihDec.toPrecision(4).toString()
              cell.y = yDec.toPrecision(4).toString()

              
              let min = resAverage[3]
              let max = resAverage[4]
              let scale = HIST_SIZE/(max-min)


              //console.log(histChart.annotation.elements)
              let pcilScaled = scale * (cell.pcil - min)
              histChart.annotation.elements['a-line-1'].options.value = pcilScaled
              histChart.annotation.elements['a-line-1'].options.label.xAdjust = +15// xPadding xAdjust
              histChart.annotation.elements['a-line-1'].options.label.content = cell.pcil

              let pcihScaled = scale * (cell.pcih - min)
              histChart.annotation.elements['a-line-2'].options.value = pcihScaled
              histChart.annotation.elements['a-line-2'].options.label.xAdjust = -15// xPadding xAdjust
              histChart.annotation.elements['a-line-2'].options.label.content = cell.pcih

              histChart.update()

              this.stateControl.processing = false

            //})
          }
          

        }
      })

      // prepare arguments to send to wasm function
      let payload = JSON.parse(JSON.stringify(cell))
      payload.data = payload.data.filter(item => this.expressionsVars[i].indexOf(item.var) !== -1)
      payload.p = 0.95

      // piecewise dists are expected to be the string
      // defining the distribution, so we match it
      payload.data.forEach(item => {
        let piecewiseMatch = this.exportedPiecewiseObj[item.dist]
        if (piecewiseMatch) {
          item.dist = piecewiseMatch
        }
      })
      console.log(payload)
      
      for (let i = 0; i < N_BURSTS; i++) {
        workerPool.forEach(worker => {
          worker.postMessage(payload);
        })
      }

    },
    createCell () {
      let cell = {
        id: broofa(),
        expr: 'X1',
        unit: 'V',
        res: {}
      }

      this.notebook.cells.push(cell)
      this.preFill()
    },
    removeCell (i) {
      this.notebook.cells.splice(i, 1)
    },
    exportCell (i) {
      if (this.stateControl.processing) return
      //let cell = this.notebook.cells[i]
      this.calcCell(i)
    }
  },
  computed: {
    exportedPiecewise () {
      return this.notebook.cells
        //.filter(item => item.export)
        .map(item => Object({id: item.id, piecewise: item.piecewise}))
    },
    exportedPiecewiseObj () {
      return this.exportedPiecewise.reduce((a, c) => {
        a[c.id] = c.piecewise
        return a
      }, {})
    },
    distributions () {
      return [
        "Uniform",
        "Normal",
        "StudentsT",
        "Triangular",
        //"LogNormal",
        "Arcsine",
        "Trap",
        "CTrap"
      ]
    },
    expressionsVars () {
      return this.notebook.cells.map(cell => {
        return cell.expr.split(/[\s+*\-()/]+/)
          // remove empty
          .filter(item => item.trim().length)
          // start with digit
          .filter(item => !item.match(/^\d/))
          .map(item => item.trim())
      })
    }
  },
  mounted () {
    let json = (window.location.href.split('/#/').slice(1).join('/#/'))
    if (json.length) {
      try {
        this.notebook = JSON.parse(decodeURI(json))
      } catch (e) {
        console.warn('Unable to parse url input.')
        console.log(json)
      }
    }
    this.preFill()

    feather.replace()
  }
}
