import constants from './constants.js';

export default function (data) {
  return {
    type: 'bar',
    plugins: ['annotation'],
    data: {
      labels: new Array(constants.HIST_SIZE).fill(0),
      datasets: [{
        backgroundColor: 'rgba(179, 204, 255, .4)',
        label: '',
        borderWidth: 1,
        data: data
      }]
    },
    options: {
      annotation: {
        annotations: [{
          drawTime: 'afterDatasetsDraw', // overrides annotation.drawTime if set
          id: 'a-line-1', // optional
          type: 'line',
          mode: 'vertical',
          scaleID: 'x-axis-0',
          value: undefined,
          borderColor: '#F14668',
          borderWidth: 2,
          label: {
            enabled: true,
            content: ''
          },
        },
        {
          drawTime: 'afterDatasetsDraw', // overrides annotation.drawTime if set
          id: 'a-line-2', // optional
          type: 'line',
          mode: 'vertical',
          scaleID: 'x-axis-0',
          value: undefined,
          borderColor: '#F14668',
          borderWidth: 2,
          label: {
            enabled: true,
            content: ''
          },                  
        }]
      },
      legend: {
        display: false
      },
      tooltips: {
        enabled: true
      },
      scales: {
        xAxes: [{
          id: 'x-axis-0',
          stacked: true,
          display: false,
          barPercentage: 1.0,
          categoryPercentage: 1.0,
          gridLines: {
            offsetGridLines: true
          },
          ticks: {
            enabled: true
          }
        }, {
          display: true,
        }],
        yAxes: [{
          ticks: {
            beginAtZero: true
          }
        }]
      }
    }
  }
}